export default {
  name: 'dataCard',
  props: {
    dataConfig: {
      type: Object,
      default: () => {},
    },
    themeType: {
      default: '1',
      type: String,
    }, //  1 瑞昌  2 万载
    list: {
      default: [],
      type: Array,
    },
